      { text: 'Django', icon: mdiLanguagePython },
      { text: 'Vue.js', icon: mdiVuejs },
      { text: 'React.js', icon: mdiReact },
      { text: 'Angular.js', icon: mdiAngular },
      { text: 'Node.js', icon: mdiNodejs },
      { text: 'Spring', icon: mdiLeaf },
